.LoginPage {
    width: 360px;
    padding: 8% 0 0;
    margin: auto;
  }
  
  .LoginForm {
    position: relative;
    z-index: 1;
    background: #FFFFFF;
    max-width: 360px;
    margin: 0 auto 100px;
    margin-top: 100px;
    padding: 45px;
    text-align: center;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
  }
  
  .Button {
    font-family: "Roboto", sans-serif;
    text-transform: uppercase;
    outline: 0;
    background: #b4cdfb;
    width: 100%;
    border: 0;
    padding: 15px;
    color: #654fa7;
    font-size: 14px;
    -webkit-transition: all 0.3 ease;
    transition: all 0.3 ease;
    cursor: pointer;
    }
  
  
    .TextInput{
      font-family: "Roboto", sans-serif;
      outline: 0;
      background: #f2f2f2;
      width: 100%;
      border: 0;
      margin: 0 0 15px;
      padding: 15px;
      box-sizing: border-box;
      font-size: 14px;
    }
  
    .Title {
      font-family: "Roboto", sans-serif;
      font-size: 20px;
      color: #654fa7;
    }

    .EmptyDiv {
      height: 50px;
    }
  
    
  
  